import { Injectable } from '@angular/core';

export interface ITool {
  id: string;
  title: string;
  icon: string;
  description: string;
  tags: string[]
}

export interface IFacetCategory {
  title: string;
  tags: IFacetValue[];
}

export interface IFacetValue {
  title: string;
  category: string;
  icon: string;
  selected: boolean;
}

@Injectable({
  providedIn: 'root'
})
export class MetadataService {

  getTool(id: string): ITool {
    return this.tools.filter(t => t.id == id)[0];
  }

  getTools() {
    return this.tools;
  }

  getFacetValueIcon(tag:string): string {
    switch(tag) {
      case 'Action:Format': return 'format_paint';
      case 'Action:Convert': return 'transform';
      case 'Output:JSON Schema': return 'account_tree';
    }

    const split = tag.split(':');

    switch (split[0]) {
      case 'Input': return 'settings_ethernet';
      case 'Output': return 'settings_ethernet';
    }

    return 'help';
  }

  getSearchFacets(search:IFacetValue[]): [ITool[], IFacetCategory[]] {
    const facets = {};
    const searchTags = search.map(t => `${t.category}:${t.title}`);
    var tools = this.tools.filter(t => searchTags.every(s => t.tags.includes(s)));
    search.forEach(s => {
      let facet = facets[s.category];
      if (!facet) {
        facet = {
          title: s.category,
          tags: {}
        }
        facets[s.category] = facet;
      }
      if (!facet.tags[s.title]) {
        facet.tags[s.title] = {
          ...s,
          selected: true
        }
      }
    });
    tools.forEach(tool => {
      tool.tags.forEach(tag => {
        const categories = tag.split(':');
        const category = categories[0];
        const subCategory = categories[1];
        let facet = facets[category];
        if (!facet) {
          facet = {
            title: category,
            tags: {}
          }
          facets[category] = facet;
        }
        if (!facet.tags[subCategory]) {
          facet.tags[subCategory] = {
            title: subCategory,
            icon: this.getFacetValueIcon(tag),
            category: category,
            selected: false
          }
        }
      });
    });
    const searchFacets = Object.keys(facets)
      .sort()
      .map(k => {
        var facet = facets[k];
        facet.tags = Object.keys(facet.tags).map(t => facet.tags[t]).sort();
        return facet;
      });

    return [tools, searchFacets]
  }
  
  private tools: ITool[] = [
    {
      id: 'json-format',
      title: 'Format JSON',
      icon: 'format_paint',
      description: 'Format JSON to make it easily readable',
      tags: ['Input:JSON', 'Output:JSON', 'Action:Format']
    },
    {
      id: 'json-to-schema',
      title: 'Create JSON Schema',
      icon: 'account_tree',
      description: 'Create JSON Schema from an existing JSON document',
      tags: ['Input:JSON', 'Output:JSON Schema', 'Action:Convert']
    },
    {
      id: 'json-to-xml',
      title: 'Convert JSON to XML',
      icon: 'transform',
      description: 'Create an XML document from an existing JSON document',
      tags: ['Input:JSON', 'Output:XML', 'Action:Convert']
    },
    {
      id: 'json-to-yaml',
      title: 'Convert JSON to YAML',
      icon: 'transform',
      description: 'Crete a YAML document  from an existing YAML document',
      tags: ['Input:JSON', 'Output:YAML', 'Action:Convert']
    }
  ];
}
