import { Component, OnInit } from '@angular/core';
import { MetadataService, ITool, IFacetCategory, IFacetValue } from '../core/metadata.service';
import { PageService } from '../core/page.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  tools: ITool[];
  facets: IFacetCategory[];
  filters: IFacetValue[] = [];
  title = 'All Tools';
  description = 'Search all tools available from Flux Tools.'

  constructor(private metadata: MetadataService, private page: PageService) {
    this.onSearchChange();
  }

  ngOnInit(): void {
    this.page.set({
      title: this.title,
      description: this.description
    });
  }

  clear() {
    this.filters = [];
    this.onSearchChange();
  }

  onSearchRemove(value:IFacetValue) {
    const index = this.filters.indexOf(value);

    if (index >= 0) {
      this.filters.splice(index, 1);
    }

    this.onSearchChange();
  }

  onSearchChange() {
    [this.tools, this.facets] = this.metadata.getSearchFacets(this.filters);
  }
}
