import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  {
    path: 'tools',
    children: [
      { path: 'json-format', loadChildren: () => import('./tools/json-format').then(m => m.JsonFormatModule)},
      // { path: 'json-path', loadChildren:() => import("./tools/json-path").then(m => m.JsonPathModule)},
      { path: 'json-to-schema', loadChildren:() => import("./tools/json-to-schema").then(m => m.JsonToSchemaModule)},
      { path: 'json-to-xml', loadChildren:() => import("./tools/json-to-xml").then(m => m.JsonToXmlModule)},
      { path: 'json-to-yaml', loadChildren:() => import("./tools/json-to-yaml").then(m => m.JsonToYamlModule)}
    ]
    
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
